import Main from '@/components/Main'
import Wrapper from '@/components/Wrapper.vue'

import Login from '@/views/Login'
import Dashboard from '@/views/Dashboard.vue'
import Buttons from '@/views/Buttons.vue'
import PostList from '@/views/post/List.vue'
import PostCreate from '@/views/post/Create.vue'
import Tables from '@/views/Tables.vue'
import Register from '@/views/Register.vue'
import FormElements from '@/views/FormElements.vue'
import Typography from '@/views/Typography.vue'
import Echarts from '@/views/Echarts.vue'
import Icon from '@/views/Icon.vue'
import Slideshow from '@/views/Slideshow.vue'
import GateSchemaForm from '@/views/GateSchemaForm.vue'

const children = [
  {
    name: '控制面板',
    path: '/',
    component: Dashboard,
    meta: {
      tagId: 'dashboard',
      title: '控制面板',
      icon: 'el-icon-cc-home'
    }
  },
  {
    // name: 'basicuielements',
    path: '/basicuielements',
    component: Wrapper,
    meta: {
      tagId: 'basicuielements',
      title: '基础界面元素',
      icon: 'el-icon-view'
    },
    children: [
      {
        name: '按钮',
        path: 'buttons',
        component: Buttons,
        meta: {
          tagId: 'buttons',
          title: '按钮'
        }
      },
      {
        name: '字体',
        path: 'typography',
        component: Typography,
        meta: {
          tagId: 'typography',
          title: '字体'
          // hideInMenu: true,
          // isCache: false,
        }
      }, {
        name: '图标',
        path: 'icon/',
        component: Icon,
        meta: {
          tagId: 'icon',
          title: '图标'
        }
      }
    ]
  },
  {
    name: '商品列表',
    path: '/',
    component: Dashboard,
    meta: {
      tagId: 'goodsList',
      title: '商品列表',
      icon: 'el-icon-goods'
    }
  },
  {
    //name: '表单',
    path: 'formelements',
    component: Wrapper,
    meta: {
      tagId: 'formelements',
      title: '表单元素',
      icon: 'el-icon-cc-edit-square'
    },
    children: [
      {
        name: '常规表单',
        path: 'formelements/',
        component: FormElements,
        meta: {
          tagId: 'formelements',
          title: '常规表单'
        }
      },
      {
        name: 'GateSchema Form',
        path: 'gateschemaform/',
        component: GateSchemaForm,
        meta: {
          tagId: 'gateschemaform',
          title: 'GateSchema Form'
        }
      }
    ]
  },
  {
    name: '表格',
    path: 'tables/',
    component: Tables,
    meta: {
      tagId: 'tables',
      title: '表格',
      icon: 'el-icon-cc-detail'
    }
  },
  {
    name: '数据图表',
    path: 'echarts/',
    component: Echarts,
    meta: {
      tagId: 'echarts',
      title: '数据图表',
      icon: 'el-icon-cc-piechart'
    }
  },
  {
    // name: '其它',
    path: 'others',
    component: Wrapper,
    meta: {
      title: '其它',
      icon: 'el-icon-menu'
    },
    children: [
      {
        name: '轮播图',
        path: 'slideshow/',
        component: Slideshow,
        meta: {
          tagId: 'slideshow',
          title: '轮播图'
        }
      },
      {
        name: '文字提示',
        path: 'register/',
        component: Register,
        meta: {
          tagId: 'register',
          title: '文字提示'
        }
      }
    ]
  },
  {
    path: 'userpages/',
    meta: {
      title: '用户界面',
      icon: 'el-icon-cc-team'
    },
    component: Wrapper,
    children: [
      {
        name: '登录',
        path: 'login/',
        component: Login,
        meta: {
          tagId: 'login',
          title: '登录'
        }
      },
      {
        name: '注册',
        path: 'register/',
        component: Register,
        meta: {
          title: '注册',
          tagId: 'register'
        }
      }
    ]
  },
  {
    path: 'post/',
    meta: {
      title: '文章管理',
      icon: 'el-icon-cc-file'
    },
    component: Wrapper,
    children: [
      {
        name: '文章列表',
        path: 'list',
        component: PostList,
        meta: {
          tagId: 'postlist',
          title: '文章列表'
        }
      },
      {
        name: 'post_create',
        path: 'create',
        component: PostCreate,
        meta: {
          tagId: 'postcreate',
          title: '创建文章',
          hideInMenu: false,
          isCache: false
        }
      }
    ]
  },
  {
    name: 'setting',
    path: 'setting/',
    meta: {
      tagId: 'setting',
      title: '系统设置',
      icon: 'el-icon-setting'
    }
  }
]

const routes = [
  {
    path: '/',
    component: Main,
    children
  },
  {
    name: 'login',
    path: '/login',
    component: Login,
    meta: {
      title: '登录'
    }
  }
]

export default routes
