<template>
    <el-container class="layout">
        <div :class="isCollapseMenu? 'aside__menu aside__menu_collapse' : 'aside__menu'">
            <el-button
                class="collpase-btn"
                type="text"
                :icon="isCollapseMenu? 'el-icon-cc-indent' : 'el-icon-cc-outdent'"
                @click="toggleMenu"
            >
            </el-button>
            <Menu
                :default-active="$route.name"
                :items="menu || defaultMenu"
                :isCollapse="isCollapseMenu"
            >
            </Menu>
        </div>
        <el-container direction="vertical">
            <el-header
                class="header"
            >
                <div class="logo"  direction="vertical">
                    <div
                        class="logo__img"
                    >
                       <i class="el-icon-location"></i> CoocaaAT one
                    </div>
                </div>
                <span>&nbsp;&nbsp;/</span>
                <Breadcrumb
                    class="breadcrumb"
                    :items="breadcrumb"
                />
                <div class="user-info">
                    <el-dropdown :hide-on-click="false" @command="handleDropdownCommand">
                        <span class="el-dropdown-link">
                            <i class="el-icon-cc-user"></i>
                            {{ $appState.user.name }}
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="logout">退出</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
                <div class="system-setting" @click="isShowSetting = !isShowSetting">
                    <i class="el-icon-setting"></i>
                </div>
            </el-header>
            <TagNav ref="tag" :init-tags="initTags" v-show="isShowTagNav"/>
            <el-main>
                <keep-alive>
                    <router-view v-if="isKeepAlive" />
                </keep-alive>
                <router-view v-if="!isKeepAlive" />
            </el-main>
        </el-container>
        <transition name="el-zoom-in-right">
            <div class="setting_side transition-box" v-show="isShowSetting">
                <div class="tagnav_switch">
                    标签导航
                    <el-switch
                        v-model="isShowTagNav"
                        active-color="#13ce66"
                        inactive-color="#ff4949">
                    </el-switch>
                </div>
            </div>
        </transition>
    </el-container>
</template>

<script>
import { Breadcrumb, Menu, TagNav } from 'admin-toolkit'
export default {
  components: {
    Menu,
    Breadcrumb,
    TagNav
  },
  props: ['menu'],
  data () {
    return {
      breadcrumb: [],
      isCollapseMenu: false,
      isShowSetting: false,
      isShowTagNav: true
    }
  },
  computed: {
    isKeepAlive () {
      const meta = this.$route.meta
      return meta && meta.isCache !== false
    },
    initTags () {
      return this.$appState.$get('tags') || []
    },
    defaultMenu () {
      const mainRoute = this.$router.options.routes.find((item) => {
        return item.path === '/'
      })
      function gen ({ name, meta = {}, children }) {
        if (!meta.hideInMenu) {
          const currentMenuItem = {
            title: meta.title,
            icon: meta.icon,
            route: name
          }
          if (children) {
            currentMenuItem.children = children.reduce((result, item) => {
              const menuItem = gen(item)
              if (menuItem) {
                result.push(menuItem)
              }
              return result
            }, [])
          }
          return currentMenuItem
        }
      }
      const items = gen(mainRoute).children
      return items
    }
  },
  methods: {
    handleDropdownCommand (command) {
      if (command === 'logout') {
        this.$logout().then(() => {
          this.$router.push({ name: 'login' })
        })
      }
    },
    toggleMenu () {
      const isCollapseMenu = !this.isCollapseMenu
      this.$appState.$set('isCollapseMenu', isCollapseMenu)
      this.isCollapseMenu = isCollapseMenu
    },
    saveTags () {
      const tags = this.$refs.tag.tags
      this.$appState.$set('tags', tags)
    }
  },
  created () {
    this.isCollapseMenu = !!this.$appState.$get('isCollapseMenu')
    this.$bus.$on('breadcrumb-change', (breadcrumb) => {
      this.breadcrumb = breadcrumb
    })
  },
  mounted () {
    window.addEventListener('beforeunload', this.saveTags)
  },
  destroyed () {
    window.removeEventListener('beforeunload', this.saveTags)
  }
}
</script>

<style lang="stylus">
.collpase-btn
    &,
    &:hover,
    &:focus
        color #606266
.collpase-btn
    width 100%
    background #fc4c02
    border-radius 0
    padding 14px 20px
.collpase-btn i
    font-size 20px
    color #fff
.collpase-btn:hover, .collpase-btn:focus
    color #fff
    background #e44907
.breadcrumb
    margin-left 10px
    padding-top 8px
.user-info
    margin-left auto
.menu:not(.el-menu--collapse)
    width 200px
    background #333
.el-submenu__title
    color #fff
.el-menu-item.is-active
    color #fff
    background #000
.el-menu-item
    color #fff
.el-menu-item, .el-submenu__title
    height 45px
    line-height 45px
.el-submenu .el-menu-item
    height 40px
    line-height 40px
.el-menu
    background #333
.el-submenu__title:hover,.el-menu-item:hover
    background:#4c4c4c
.el-submenu.is-opened .el-submenu__title
    background #2d2d2d
.el-menu-item.is-active
    border-left 3px solid #fc4c02
.el-submenu .el-menu
    background #1e1e1e
</style>
