<template>
    <el-row class="card">
            <el-col :span="24" class="padd-row" >
              <el-row>
                <div class="flex-row white-bg source">
                  <div class="h3 card-title">GateSchemaForm</div>
                  <div class="grid-content bg-purple demo-button source">
                      <GateSchemaForm
                          :schema="schema"
                          v-model="formData"
                      />
                  </div>
                </div>
               </el-row>
            </el-col>
        </el-row>
</template>

<script>
import { GateSchemaForm } from 'admin-toolkit'
import _ from 'gateschema'
const schema = _
  .map({
    name: _
      .map({
        firstName: _.required
          .$msg('First Name is required')
          .string.notEmpty.other('form', {
            layout: 'inline',
            cols: {
              item: 6,
              label: 0,
              wrapper: 24
            },
            placeholder: 'First Name'
          }),
        lastName: _.required.string.notEmpty.other('form', {
          layout: 'inline',
          cols: {
            item: {
              span: 8,
              offset: 1
            },
            label: 0,
            wrapper: 24
          },
          placeholder: 'Last Name'
        })
      })
      .other('form', {
        class: {
          'user-name': true
        }
      })
  })
  .map({
    email: _
      .switch('/phone', [
        {
          case: _.required,
          schema: _.optional
        },
        {
          case: _.any,
          schema: _.required.$msg('email or phone is required')
        }
      ])
      .string.notEmpty.format('email'),
    phone: _.switch('/email', [
      {
        case: _.required,
        schema: _.optional
      },
      {
        case: _.any,
        schema: _.required.$msg('email or phone is required')
      }
    ]).string.notEmpty,
    captcha: _.string.switch('/phone', [
      {
        case: _.required,
        schema: _.required
      },
      {
        case: _.any,
        schema: _.optional.other('form', {
          hidden: true
        })
      }
    ]),
    password: _.required.string.notEmpty.other('form', {
      type: 'password'
    }),
    gender: _.enum({
      male: 0,
      female: 1
    }),
    selectGender: _
      .enum({
        male: 0,
        female: 1
      })
      .other('form', {
        component: 'Select',
        placeholder: 'Please Select'
      }),
    languages: _
      .enumList({
        c: 1,
        java: 4,
        scala: 5,
        haskell: 7,
        javascript: 8
      })
      .other('form', {
        placeholder: 'Please Select',
        disabledItems: {
          javascript: true
        }
      }),
    selectLanguages: _
      .enumList({
        c: 1,
        go: 2,
        'c++': 3,
        java: 4,
        scala: 5,
        python: 6,
        haskell: 7,
        javascript: 8
      })
      .other('form', {
        component: 'Select',
        placeholder: 'Please Select',
        disabledItems: {
          java: true
        }
      }),
    rememberMe: _.required.boolean,
    date: _.required.string.other('form', {
      component: 'DatePicker'
    }),
    dateTime: _.optional.string.other('form', {
      component: 'DateTimePicker'
    }),
    timestamp: _.required.number.other('form', {
      component: 'DateTimePicker',
      valueType: 'millisecond'
    }),
    address: _.list(
      _.required.string.other('form', {
        cols: {
          label: 0,
          wrapper: 24
        }
      })
    ),
    passenger: _.required.list(
      _.map({
        name: _.required.string,
        age: _.optional.number
      })
    )
  })
  .other('form', {
    cols: {
      label: 6,
      wrapper: 18
    },
    footer: {
      cols: {
        label: 0,
        wrapper: 24
      },
      showSubmit: true,
      submitText: 'Submit Text'
    },
    layout: 'vertical'
  })

export default {
  components: {
    GateSchemaForm
  },
  data () {
    return {
      schema,
      formData: {},
      input: '',
      input1: '',
      inputPass: ''
    }
  }
}
</script>
